@media (max-width: 72rem) {
    .mood-div {
        gap: 2vw;
    }
}
@media (max-width: 55rem) {
    .mood-title {
        font-size: 4rem;
    }
    .mood-card {
        height: 80%;
    }
    .mood-image {
        width: 90%;
    }
}
@media (max-width: 40rem) {
    .mood-title {
        font-size: 3rem;
        margin-bottom: 5vh;
    }
    .music-box {
        bottom: 1rem;
        left: -1rem;
    }
    .music-text {
        font-size: 3rem;
    }
    .mood-foot-cred {
        font-size: 1.5rem;
    }
}
@media (max-width: 28rem) {
    .mood-title {
        font-size: 3rem;
        margin-bottom: 5vh;
    }
    .mood-div {
        display: grid;
        grid-template-rows: 35% 35%;
        grid-template-columns: 30% 30%;
        row-gap: 10%;
        column-gap: 10%;
    }
    .mood-name {
        font-size: 2rem;
    }
    .music-box {
        bottom: 7rem;
        left: -1rem;
    }
    .mood-foot {
        position: absolute;
        bottom: 7rem;
        right: -1rem;
    }
}
